import React from 'react';
import { Redirect, Switch, Route } from 'react-router-dom';
import { connect } from 'react-redux'; //Rx

import './App.css';

import HomePage from './pages/homepage/homepage.component';
import ShopPage from './pages/shop/shop.component';
import SignInAndSignUpPage from './pages/sign-in-and-sign-up/sign-in-and-sign-up-out.component';
import Header from './components/header/header.component';
import { auth, createUserProfileDocument } from './firebase/firebase.utils';
import { setCurrentUser } from './redux/user/user.actions'; //Rx

//Let's convert function into class component
class App extends React.Component {

  /*constructor() {
    super();

    this.state = {
      currentUser: null
    }
  }*/

  unsubscribeFromAuth = null;

  componentDidMount() {
    const { setCurrentUser } = this.props; //Rx (this.props)

    this.unsubscribeFromAuth = auth.onAuthStateChanged(async userAuth => {
      
      if(userAuth)
      {
        const userRef = await createUserProfileDocument(userAuth);

        userRef.onSnapshot(snapShot => {
          /*this.setState({
            currentUser: {
              id: snapShot.id,
              ...snapShot.data()
            }
          })*/

          setCurrentUser({
            id: snapShot.id,
            ...snapShot.data()
          });
        });
      }

      /*this.setState({
        currentUser: userAuth
      });*/
      setCurrentUser(userAuth);
    });
  }

  componentWillUnmount() {
    this.unsubscribeFromAuth();
  }

  render() {
    return (
      <div>
        <Header /*currentUser={this.state.currentUser}*/ />      
        <Switch>
  
          <Route path="crwn-clothing/" exact>
              <Redirect to="crwn-clothing" />
          </Route>
  
          <Route exact path="/crwn-clothing" component={HomePage} />
          <Route exact path="/crwn-clothing/shop" component={ShopPage} />
          <Route exact path='/crwn-clothing/signin' component={SignInAndSignUpPage} />
        </Switch>     
      </div>
    );
  }
}

const mapDispatchToPtops = dispatch => ({
  setCurrentUser: user => dispatch(setCurrentUser(user))
});

// export default App;
export default connect(null, mapDispatchToPtops)(App);
